import request from "@/request";
import store from '@/common/store';
import config from '@/config/index.config.js';
import userAction from '@/utils/userAction.js';

//newrequest
let $http = new request({
	//
	baseUrl: config.baseUrl,
	//
	fileUrl: config.baseUrl,
	// url
	defaultUploadUrl: "api/common/v1/upload_image",
	//content-type
	header: {
		'Content-Type': 'application/json;charset=UTF-8',
		// 'project_token': config.projectToken, //token
	},
	config: {
		isPrompt: true,
		load: false,
		isFactory: true,
		resend: 0,
	}
});
// token
$http.getQnToken = function(callback) {
	//
	$http.get("api/common/v1/qn_upload").then(data => {
		/*
		 *
		 *visitPrefix:
		 *token:token
		 *folderPath:
		 *region:  SCN
		 */
		callback({
			visitPrefix: data.visitPrefix,
			token: data.token,
			folderPath: data.folderPath
		});
	});
}
//
$http.requestStart = function(options) {
	console.log("", options);
	if (options.load) {
		//
		store.commit("setLoadingShow", true);
	}
	// 
	if (options.method == "FILE" && options.maxSize) {
		// : options.maxSize 
		let maxSize = options.maxSize;
		for (let item of options.files) {
			if (item.size > maxSize) {
				setTimeout(() => {
					uni.showToast({
						title: "",
						icon: "none"
					});
				}, 500);
				return false;
			}
		}
	}
	if (options.url) {
		let sessionId = uni.getStorageSync("sessionid")
		if (sessionId) {
			options.header['Cookie'] = sessionId
		}
		//token
		let url = options.url.substring(options.url.lastIndexOf('/') + 1);
		if(config.apiWhitePath.indexOf(url) < 0) {
			//
			let userData = uni.getStorageSync('userData')
			if (userData && userData.userId) {
				options.header['userId'] = userData.userId
			}
			let projectId = uni.getStorageSync('projectId')
			if (projectId && projectId > 0) {
				options.header['projectId'] = projectId
			}
		}
		//classID
		if(options.data && options.data.classId) {
			options.header['classId'] = options.data.classId
		}
	}
	return options;
}
//
$http.requestEnd = function(options) {
	//
	if (options.load) {
		// 
		store.commit("setLoadingShow", false);
	}
}
//
$http.dataFactory = async function(res) {
	console.log("", {
		url: res.url,
		resolve: res.response,
		header: res.header,
		data: res.data,
		method: res.method,
	});
	if (res.response.header && res.response.header['Set-Cookie']) {
		let sessionId = res.response.header['Set-Cookie'].split(" ")[0]
		uni.setStorageSync('sessionid', sessionId); //CookieStorage
	}
	if (res.response.statusCode && res.response.statusCode == 200) {
		let httpData = res.response.data;
		if (typeof(httpData) == "string") {
			httpData = JSON.parse(httpData);
		}
		/*********()*********/

		//
		// if (httpData.success || httpData.code == 200) {
		if (httpData.code == 200) {
			// (then)
			return Promise.resolve(httpData);
		} else if (httpData.code == "1000" || httpData.code == "1001" || httpData.code == 1100 || httpData.code == 402) {
			store.commit("emptyUserInfo");

			// onLogin();

			// (catch)
			return Promise.reject({
				statusCode: 0,
				errMsg: "request" + (httpData.info || httpData.msg),
				data: res.data
			});
		} else if (httpData.code == "401") { // token
			setTimeout(() => {
				uni.showToast({
					icon: 'none',
					title: httpData.info || httpData.msg,
					duration: 1500
				});
			}, 0);
		} else { //   
			if (res.isPrompt) {
				setTimeout(() => {
					uni.showToast({
						icon: 'none',
						title: httpData.info || httpData.msg,
						duration: 3000
					});
				}, 0);
			}
			let data = {}
			if(res.response && res.response.data) {
				data = res.response.data
			}			
			// (catch)
			return Promise.reject({
				statusCode: 0,
				errMsg: "request" + (httpData.info || httpData.msg),
				data: data
			});
		}
		/*********()*********/
	} else {
		// (catch)
		return Promise.reject({
			statusCode: res.response.statusCode,
			errMsg: "request",
			data: res.data
		});
	}
};
// 
$http.requestError = function(e) {
	// e.statusCode === 0 
	if (e.statusCode === 0) {
		throw e;
	} else {
		console.log(e);
		uni.showToast({
			title: "",
			icon: "none"
		});
	}
}
export default $http;
